import React from "react";
import classes from '../../CommonAppUtilities/TopInfoBar.module.css';
import TopInfoBarInstructor from '../../CommonAppUtilities/TopInfoBarInstructor';
import TopInfoBarButton from '../../CommonAppUtilities/TopInfoBarButton.js';
import { useHistory } from 'react-router-dom';


const  TopInfoBarView3 =(props) =>  {

   let history = useHistory();

   const moveToMain=()=>{
   history.push('/dashboard/main');
   }

   const moveToView2=()=>{
   history.push('/dashboard/view2');
   }










  return (

  <div className={classes.topInfoBar}>

       <TopInfoBarInstructor/>
      
       <TopInfoBarButton  onPress={moveToMain}  buttonName={'Main'}/>

       <TopInfoBarButton  onPress={moveToView2} buttonName={'View2'}/>

       <TopInfoBarButton styles={props.styles}  buttonName={'View3'}/>
	  




  </div>

);	
}

export default TopInfoBarView3;
