import React,{useState} from 'react'; 
import classes from './HeaderLeft.module.css';
import companyLogo from './img/logoBlueWhite.PNG'
import {Route,Switch,Link, BrowserRouter} from 'react-router-dom';

import {BiDotsVertical} from 'react-icons/bi';

function HeaderLeft(props){




return (

 <div className={classes.headerLeft} > 
      
      <img className={classes.logo} src={companyLogo} alt='edr Logo' />

      <button className={classes.ExpConButton} onClick={props.onPress}> <BiDotsVertical className={classes.ExpConIcon} /></button>	

      <Link to='/courseinfo' className={classes.courseName}> Classical Mechanics-I<sup className={classes.superScript}>@Stanford</sup> </Link>	


  </div>

);


}

export default HeaderLeft;

