import React from "react";
import classes from '../../CommonAppUtilities/TopInfoBar.module.css';
import TopInfoBarInstructor from '../../CommonAppUtilities/TopInfoBarInstructor';
import TopInfoBarButton from '../../CommonAppUtilities/TopInfoBarButton.js';
import { useHistory } from 'react-router-dom';


const  TopInfoBarCurrent =(props) =>  {

   let history = useHistory();

   const moveToPrevious=()=>{

   console.log("move to previous");	   
   history.push('/class/previous');
   }

   const moveToAll=()=>{
   history.push('/class/all');
   }










  return (

  <div className={classes.topInfoBar}>

       <TopInfoBarInstructor/>
      
       <TopInfoBarButton  styles={props.styles}  buttonName={'Current'}/>

       <TopInfoBarButton  onPress={moveToPrevious} buttonName={'Previous'}/>

       <TopInfoBarButton onPress={moveToAll}   buttonName={'All'}/>
	  




  </div>

);	
}

export default TopInfoBarCurrent;
